.back-nav{ 
    background-color: #011740;
}

.nav-container-2{
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;

    img {
        width: 150px;
    }

    button {
        border: none;
        height: 50px;
        width: 50px;

        img{
            width: 40px;
            height: 40px;
        }
    }
}

.nav-container{
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-around;

    img {
        width: 150px;
    }
}


@media (max-width: 800px) {
    .nav-container{
        display: none;
    }
}

@media (min-width: 800px) {
    .nav-container-2{
        display: none;
    }
}

.nav-hide{
    display: none;
}

.nav-show{
    position: absolute;
    height: 200px;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0 30px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-end;
    font-size: 40px;
    font-weight: lighter;
}